import { useState } from 'react';
import './App.css';
import Puzzle from './Puzzle';
import NavBar from './NavBar';
import Notas from './Notas';
import Rules from './Rules';
import { ethers, BigNumber } from 'ethers';
import PuzzleJSON from './Puzzle2.json';
import { Box, Button, Flex, Image, Link, Spacer } from '@chakra-ui/react';

// https://exce1ance.github.io/blockchain-project/
// npm run deploy
const RINKEBY_RPC_URL = process.env.RINKEBY_RPC_URL
const CONTRACT_ADDRESS = process.env.REACT_APP_CONTRACT_ADDRESS

function App() {
  const [accounts, setAccounts] = useState([]);
  const [tokenId, setTokenId] = useState();
  const [chainId, setchainId] = useState();

  console.log("contract add:", CONTRACT_ADDRESS)
  async function checkPieces() {

    let provider = new ethers.providers.AlchemyProvider("rinkeby", RINKEBY_RPC_URL)
    const contract = new ethers.Contract(
      CONTRACT_ADDRESS,
      PuzzleJSON.abi,
      provider
    );
    try {
      const response = await contract.tokenId();
      setTokenId(response.toNumber() - 1)
    } catch (err) {
      console.log("error: ", err)
    }

    provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    provider.on("network", async () => {
      const network = await provider.getNetwork();
      setchainId(network.chainId);

      // window.ethereum.on('accountsChanged', function (accounts) {
      //   // Time to reload your interface with accounts[0]!
      // })

    })
  }

  checkPieces()

  return (

    <div className="App">
      <NavBar className="NavBar" accounts={accounts} setAccounts={setAccounts} setTokenId={setTokenId} />
      {/* <MainMint accounts={accounts} setAccounts={setAccounts} /> */}
      <div className="notas">
        <Notas tokenId={tokenId} chainId={chainId} accounts={accounts} setTokenId={setTokenId} />
      </div>

      <Flex justify="center" align="center">
        <Box>
          <div>
            <Flex justify="center" >
              <Rules />
              <Puzzle tokenId={tokenId} chainId={chainId} />
            </Flex>
          </div>
        </Box>
      </Flex>
    </div>

  );
}

export default App;
