import { useState } from 'react';
import { ethers, BigNumber } from 'ethers';
import PuzzleJSON from './Puzzle2.json';
import { Box, Button, Flex, Input, Text } from '@chakra-ui/react';



const Notas = ({ tokenId, chainId, accounts, setTokenId }) => {
    const isConnected = Boolean(accounts[0]);
    const [isMinting, setIsMinting] = useState();
    const [mintAmount, setMintAmount] = useState(1);
    const CONTRACT_ADDRESS = process.env.REACT_APP_CONTRACT_ADDRESS

    async function handleMint() {
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
                CONTRACT_ADDRESS,
                PuzzleJSON.abi,
                signer
            );
            try {
                const response = await contract.mint(BigNumber.from(mintAmount), { value: 100000000000000 * mintAmount }); // Solidity requiere que se lo pase como BigNumber
                setIsMinting(true)
                await response.wait();
                setIsMinting(false)
                // console.log('response: ', response)
                const response2 = await contract.tokenId();
                setTokenId(response2.toNumber() - 1)
                console.log(`NavBar: ${response2.toNumber() - 1}`)
            } catch (err) {
                console.log("error: ", err)
            }
        } else {
            console.log("window.ethereum = false")
        }
    }

    const handleDecrement = () => {
        if (mintAmount === 1) return;
        setMintAmount(mintAmount - 1);
    }

    const handleIncrement = () => {
        if (mintAmount >= 25 - tokenId) return;
        setMintAmount(mintAmount + 1);
    }

    return (
        <>
            <Flex justify="center" align="center">
                <Box>
                    <div>
                        {tokenId > 25 ?
                            <Text fontSize="40px" textShadow="0 2px 2px #000000">NFT Completed! Pieces owners will receive Full Art NFT.</Text>
                            :
                            <Text fontSize="40px" textShadow="0 2px 2px #000000">Still {tokenId === undefined ? "-" : (25 - tokenId)} NFT parts to mint!</Text>
                        }
                        <Text fontSize="35px" color="#D6517D">{chainId === 4 ? null : "Please Connect to Rinkeby Network to use this page."}</Text>
                    </div>
                    {isConnected ? (
                        <div>
                            <Flex justify="center" align="center">
                                <Button background="black"
                                    borderRadius="5px"
                                    boxShadow="0px 2px 2px 1px #0F0F0F"
                                    color="white"
                                    cursor="pointer"
                                    fontFamily="inherit"
                                    padding="15px"
                                    marginBottom="10px"
                                    fontSize="35px"
                                    width="80px"
                                    onClick={handleDecrement}>-</Button>
                                <Input readOnly
                                    fontFamily="inherit" width="250px" height="65px" textAlign="center"
                                    paddingLeft="19px" type="number" value={mintAmount}
                                    fontSize="35px" marginBottom="10px">
                                </Input>
                                <Button background="black"
                                    borderRadius="5px"
                                    boxShadow="0px 2px 2px 1px #0F0F0F"
                                    color="white"
                                    cursor="pointer"
                                    fontFamily="inherit"
                                    padding="15px"
                                    marginBottom="10px"
                                    fontSize="35px"
                                    width="80px"
                                    onClick={handleIncrement}>+</Button>
                            </Flex>
                            <Button background="black"
                                borderRadius="5px"
                                boxShadow="0px 2px 2px 1px #0F0F0F"
                                color="white"
                                cursor="pointer"
                                fontFamily="inherit"
                                padding="15px"
                                marginBottom="10px"
                                fontSize="35px"
                                onClick={handleMint}>{isMinting ? "Minting..." : "Mint Now"}</Button>
                        </div>
                    ) : (
                        <Text marginTop="10px" fontSize="35px" color="#D6517D">You must be connected to Mint!</Text>
                    )}

                </Box>
            </Flex>
        </>
    )
}

export default Notas;