import { Link, Text, Box, background } from '@chakra-ui/react';
import recuadro from './assets/golden-frame.png';

const Rules = () => {

    return (
        <div style={{
            position: 'relative', marginRight: "200px", display: 'flex', justifyContent: 'center', alignItems: 'center'
        }}>
            <img alt="goldenFrame" style={{
                width: 1400, height: 1130, position: 'absolute', marginRight: "200px"
            }} src={recuadro}>
            </img>
            <div style={{
                width: 1100, height: 770, position: 'relative', marginTop: "150px", textAlign: "left", marginRight: "200px"
            }}>
                <Text textAlign="center" fontSize="40px">About:</Text>
                <Text fontSize="35px">1. Connect your Metamask to Rinkeby Network</Text>
                <Text fontSize="35px">2. Get some fake ETH from here: <Link href="https://rinkebyfaucet.com">https://rinkebyfaucet.com</Link></Text>
                <Text fontSize="35px">3. Select how many parts you want to mint and click on Mint Now!</Text>
                <Text fontSize="35px">4. Wait for the picture to complete and receive the Full Art NFT</Text>
                <Text fontSize="35px">5. You can sell your part at <Link href="https://testnets.opensea.io">Opensea</Link> or you can buy other parts from each owner</Text>
                <Text fontSize="35px">6. If you own all 25 parts, you can claim the Full Art NFT by burning all 25 pieces</Text>
            </div>
        </div>

    )

}

export default Rules;